import React, { Children, cloneElement, useEffect, useRef, useState } from 'react';
import { element, number, oneOf, oneOfType, shape, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';
import { Manager, Reference, Popper } from 'react-popper';
import MenuCurtain from '../menu-curtain';
import Menu from '../menu';

const CSS_PREFIX = 'vds-navmenu';

const keyCode = {
  DOWN: 40,
  END: 35,
  ESC: 27,
  HOME: 36,
  PAGEDOWN: 34,
  PAGEUP: 33,
  RETURN: 13,
  SPACE: 32,
  TAB: 9,
  UP: 38
};

const isPrintableCharacter = str => str.length === 1 && str.match(/\S/);
const getCharFilterIndex = ({
  char,
  count,
  focusableItems,
  start
}) => {
  for (let i = start; i < count; i++) {
    if (char === focusableItems[i].textContent.trim().toLowerCase().substring(0, 1)) {
      return i;
    }
  }
  return -1;
};

const Navmenu = ({
  children,
  className,
  id = shortid.generate(),
  menu,
  modifiers,
  placement,
  ...remainingProps
}) => {
  const [open, setOpen] = useState(false);
  const [focusedIndex, setFocusedIndex] = useState(0);
  const [focusableItems, setFocusableItems] = useState([]);
  const [disabledFocusReturn, setDisabledFocusReturn] = useState(true);
  const uniqueId = useRef(id);
  const uid = uniqueId.current;
  const listboxRef = useRef();
  const buttonRef = useRef();

  useEffect(() => {
    if (listboxRef.current) {
      setFocusableItems(listboxRef.current.querySelectorAll(
        'button:not([disabled]):not(.vds-state--disabled), [href]:not(.vds-state--disabled)'
      ));
    }
  }, []);

  useEffect(() => {
    if (open) {
      focusableItems[focusedIndex].focus();
    }

    if (!open && buttonRef.current && !disabledFocusReturn) {
      buttonRef.current.focus();
      setDisabledFocusReturn(false);
    }
  }, [disabledFocusReturn, focusedIndex, focusableItems, open]);

  const handleClick = e => {
    const isButton = e.target.tagName.toLowerCase() === 'button';
    const isLink = e.target.tagName.toLowerCase() === 'a';

    setOpen(!open);
    if (!isButton && !isLink) {
      e.preventDefault();
    }
  };

  const handleKeyDown = e => {
    const code = e.charCode || e.keyCode;
    const count = focusableItems.length;
    const isButton = e.currentTarget.tagName.toLowerCase() === 'button';
    const isListBox = e.currentTarget.tagName.toLowerCase() === 'ul';
    let focusId = 0;
    if (isListBox) {
      setDisabledFocusReturn(false);
    }

    if (code === keyCode.END) {
      e.preventDefault();
      if (isButton) {
        setOpen(true);
      }

      if (count) {
        focusId = count - 1;
        setFocusedIndex(focusId);
        focusableItems[focusId].focus();
      }
    }

    if (code === keyCode.HOME) {
      e.preventDefault();
      if (isButton) {
        setOpen(true);
      }

      if (count) {
        focusId = 0;
        setFocusedIndex(focusId);
        focusableItems[focusId].focus();
      }
    }

    if (code === keyCode.UP) {
      e.preventDefault();
      if (isButton) {
        setOpen(true);
        focusId = focusedIndex || count - 1;
      }

      if (isListBox && count) {
        focusId = focusedIndex - 1 < 0 ? count - 1 : focusedIndex - 1;
      }

      if (count) {
        setFocusedIndex(focusId);
        focusableItems[focusId].focus();
      }
    }

    if (code === keyCode.DOWN) {
      e.preventDefault();
      if (isButton) {
        setOpen(true);
        focusId = focusedIndex || 0;
      }

      if (isListBox && count) {
        focusId = (focusedIndex + 1) % count;
      }

      if (count) {
        setFocusedIndex(focusId);
        focusableItems[focusId].focus();
      }
    }

    if (code === keyCode.ENTER) {
      if (isButton) {
        setOpen(true);
      }

      if (isListBox) {
        setOpen(false);
      }
    }

    if (code === keyCode.ESC) {
      e.preventDefault();
      setOpen(false);
    }

    // Tab
    if (code === keyCode.TAB && isListBox) {
      setDisabledFocusReturn(true);
      setOpen(false);
    }

    if ( e.ctrlKey || e.altKey || e.metaKey || (e.keyCode === keyCode.SPACE) || (e.keyCode === keyCode.RETURN)) {
      return;
    }

    if (isPrintableCharacter(e.key)) {
      const char = e.key.toLowerCase();
      const start = focusedIndex + 1 === count ? 0 : focusedIndex + 1;

      let match = getCharFilterIndex({
        char,
        count,
        focusableItems,
        start
      });

      if (isButton) {
        setOpen(true);
      }

      if (match === -1) {
        match = getCharFilterIndex({
          char,
          count,
          focusableItems,
          start: 0
        });
      }

      if (match > -1) {
        setFocusedIndex(match);
        focusableItems[match].focus();
      }
    }
  };

  return (
    <Manager>
      <div
        className={classnames(CSS_PREFIX, className)}
        {...remainingProps}
      >
        <Reference>
          {({ ref }) => (
            <span ref={ref}>
              {
                cloneElement(children, {
                  active: open,
                  'aria-controls': `${CSS_PREFIX}-listbox-${uid}`,
                  'aria-expanded': open ? 'true' : undefined,
                  'aria-haspopup': menu ? 'true' : 'false',
                  id: uid,
                  onClick: handleClick,
                  onKeyDown: handleKeyDown,
                  ref: buttonRef
                })
              }
            </span>
          )}
        </Reference>
        <Popper
          modifiers={modifiers}
          placement={placement}
        >
          {({ ref, style }) => (
            <Menu
              id={`${CSS_PREFIX}-menu-${uid}`}
              position="absolute"
              ref={ref}
              show={open}
              style={{ ...style, overflowY: 'inherit' }}
            >
              {
                cloneElement(menu, {
                  'aria-labelledby': uid,
                  children: Children.map(menu.props.children, (child, k) => cloneElement(child, {
                    children: Children.map(child.props.children, (c, i) => cloneElement(c, {
                      key: `${uid}-${k}-${i}`,
                      role: 'menuitem',
                      tabIndex: '-1'
                    })),
                    key: `${uid}-${k}`,
                    role: 'none'
                  })),
                  id: `${CSS_PREFIX}-listbox-${uid}`,
                  onClick: handleClick,
                  onKeyDown: handleKeyDown,
                  ref: listboxRef,
                  role: 'menu'
                })
              }
            </Menu>
          )}
        </Popper>
      </div>
      <MenuCurtain
        onClick={handleClick}
        show={open}
      />
    </Manager>
  );
};

Navmenu.propTypes = {
  /**
  * @ignore
  */
  children: element,

  /**
   * @ignore
   */
  className: string,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Menu
   */
  menu: element.isRequired,

  /**
   * Modifiers
   */
  modifiers: shape({}),

  /**
   * Placement
   */
  placement: oneOf([
    'auto',
    'bottom-end',
    'bottom-start',
    'bottom',
    'left-end',
    'left-start',
    'left',
    'right-end',
    'right-start',
    'right',
    'top-end',
    'top-start',
    'top'
  ])
};

Navmenu.defaultProps = {
  modifiers: {
    offset: {
      enabled: true,
      offset: '0, 15px'
    }
  },
  placement: 'bottom-start'
};

Navmenu.displayName = 'Navmenu';

export default Navmenu;
